import React, { Component } from 'react'
import Title from "../../title/"
import "./style/tabulate.css"
import Stars from "../../stars/index"
import StateType from "@/components/stateType/"
import TableBox from "@/components/table/"
import Bread from "@/components/Bread/"

class Tabulate extends Component {
    state = {
        starsArr: [
            {
                describe: "最勤快",
                img: "https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3024664728,892882405&fm=26&gp=0.jpg",
                name: "王霞",
                region: "山西晋城",
                number: 500
            },
            {
                describe: "最勤快",
                img: "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1502851994,1174410470&fm=26&gp=0.jpg",
                name: "王霞",
                region: "山西晋城",
                number: 500
            }
        ]
    }
    render() {
        let { starsArr } = this.state;
        return (
            <div className="tabulate-box">
                <Bread history={this.props.history}/>
                <div className="tabulate-box-top">
                    <Title title="积极奋进达人" />
                    <Stars starsArr={starsArr} />
                </div>
                <div className="tabulate-box-content">
                    <Title title="主管竞争力排名" />
                    <StateType btnOpen={false} sortOpen={true} />
                    <TableBox />
                </div>
            </div>
        )
    }
}


export default Tabulate;