import React, { Component } from 'react'
import { DatePicker, Select, Button } from 'antd';
import moment from 'moment';

const { RangePicker } = DatePicker;
const { Option } = Select;

class StateType extends Component {
    state = {
        searchCon: "展开查询︾",
        open: false,
        ind: 0
    }
    static defaultProps = {
        dateOpen: true,
        btnOpen: true,
        sortOpen: false,
        topArr: ["导出", "本周", "本月", "上月", "本季度", "半年", "全年"]
    }
    render() {
        let { topArr, dateOpen, btnOpen, sortOpen } = this.props
        let { searchCon, open, ind } = this.state
        return (
            <div className="state-box">
                <div className="state-box-con">
                    {
                        topArr.map((item, index) =>
                        <span key={index} className={index === ind ? "active" : ""} onClick={() => {
                            this.setState({
                                ind: index
                            })
                        }}
                        >{item}
                        </span>)
                    }
                    {
                        dateOpen && <div className="data-box">
                        <RangePicker
                            ranges={{
                                Today: [moment(), moment()],
                                'This Month': [moment().startOf('month'), moment().endOf('month')]
                            }}
                        />
                        </div>
                    }
                    {
                        sortOpen && <span className="sortName">积分综合排名</span>
                    }
                    {
                        btnOpen && <button onClick={() => {
                            if (searchCon === "展开查询︾") {
                                this.setState({
                                    searchCon: "关闭查询︽",
                                    open: true
                                })
                            } else {
                                this.setState({
                                    searchCon: "展开查询︾",
                                    open: false
                                })
                            }
                        }}
                                   >{searchCon}
                        </button>
                    }
                </div>
                {
                    open && <div className="state-box-select">
                        <div className="select-item">
                            <span>客户类型：</span><Select defaultValue="lucy" style={{ width: 120 }} onChange={this.handleChange}>
                                <Option value="jack">Jack</Option>
                            </Select>
                        </div>
                        <div className="select-item">
                            <span>营销区域：</span><Select defaultValue="lucy" style={{ width: 120 }} onChange={this.handleChange}>
                                <Option value="jack">Jack</Option>
                            </Select>
                        </div>
                        <div className="select-item">
                            <span>客户经理：</span><Select defaultValue="lucy" style={{ width: 120 }} onChange={this.handleChange}>
                                <Option value="jack">Jack</Option>
                            </Select>
                        </div>
                        <div className="select-item">
                            <span>是否标注：</span><Select defaultValue="lucy" style={{ width: 120 }} onChange={this.handleChange}>
                                <Option value="jack">Jack</Option>
                            </Select>
                        </div>
                        <div className="select-item">
                            <span>时间选择：</span>
                            <RangePicker
                                ranges={{
                                    Today: [moment(), moment()],
                                    'This Month': [moment().startOf('month'), moment().endOf('month')]
                                }}
                            />
                        </div>
                        <div className="select-btn">
                            <Button style={{ background: "#3A98FA", color: "#fff" }}>查询</Button>
                            <Button style={{ background: "#D8D8D8", color: "#436CB0" }}>清除</Button>
                        </div>
                    </div>
                }

            </div>
        )
    }
}


export default StateType;