import React, { Component } from 'react'
import {Breadcrumb,Icon} from "antd"
import {NavLink} from "react-router-dom"
import "./index.scss"
import {crumbs,removeCrumbs} from "@/router/stair/"
class Index extends Component {
    state={
        renderData:[]
    }
    render() {
        let {renderData}=this.state
        return <Breadcrumb separator="" className="bread">
            {
                renderData&&renderData.map((item,index)=>
                <Breadcrumb.Item key={index}>
                    {item.meta.title&&<NavLink to={item.path}>
                        {item.meta.title}
                    </NavLink>}
                    <Icon type="close" onClick={this.remove.bind(this,index)}/>
                </Breadcrumb.Item>)
            }
        </Breadcrumb>
    }
    remove(index){
        let renderData = removeCrumbs(index);
        this.setState({renderData})
    }
    componentDidMount(){
        let renderData = crumbs(this.props.history.location.pathname)
        this.setState({renderData})
    }
}
export default Index