import React, { Component } from 'react'
import { Tag } from 'antd';

class Stars extends Component {
    render() {
        let { starsArr } = this.props
        return (
            <div className="stars-box">
                {
                    starsArr.map((item, index) => <div key={index} className="stars-box-item">
                        <div className="img-box">
                            <img src={item.img} alt=""/>
                            <p><Tag color="green">{item.describe}</Tag></p>
                        </div>
                        <div className="con">
                            <h2>{item.name}</h2>
                            <p>{item.region}</p>
                            <b>{item.number}</b>
                        </div>
                    </div>)
                }
            </div>
        )
    }
}


export default Stars